import Vue from 'vue'
import Router from 'vue-router'
import About from './views/About.vue'
import Projects from './views/Projects.vue'
import Resume from './views/Resume.vue'
import Contact from './views/Contact.vue'





Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect:'/about',
    },
    {
      path:'/about',
      name:'About',
      component:About
    },
    {
      path:'/projects',
      name:'Projects',
      component:Projects
    },
    {
      path:'/resume',
      name:'Resume',
      component:Resume
    },
    {
      path:'/contact',
      name:'Contact',
      component:Contact
    },
  
  
  
    
  ]
})
